.content {
  margin-top: 60px; }

input, textarea, input:focus, textarea:focus {
  border: 1px solid #cccccc;
  outline: none; }

.navbar {
  background: #FFFFFF;
  height: 60px;
  padding-left: 20px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999999;
  width: 100%; }
  .navbar {
    *zoom: 1; }
  .navbar:before, .navbar:after {
    content: "";
    display: table; }
  .navbar:after {
    clear: both; }
  .navbar .logo {
    float: left;
    height: 40px;
    margin-top: 10px;
    padding-right: 20px; }
    .navbar .logo img {
      height: 100%; }
  .navbar .links {
    float: left; }
    .navbar .links {
      *zoom: 1; }
    .navbar .links:before, .navbar .links:after {
      content: "";
      display: table; }
    .navbar .links:after {
      clear: both; }
    .navbar .links .link {
      float: left;
      line-height: 60px;
      margin: 0 10px;
      cursor: pointer; }

.home-page {
  background: #FFFFFF;
  padding: 40px; }
  .home-page .piece {
    width: 100%;
    margin: 20px 0; }
    .home-page .piece .picture {
      margin: 0 auto;
      width: 50%; }
      .home-page .piece .picture img {
        width: 100%;
        opacity: 0.5; }
        .home-page .piece .picture img:hover {
          opacity: 0.75; }
      .home-page .piece .picture.large {
        width: 100%; }
        .home-page .piece .picture.large img {
          opacity: 1; }
    .home-page .piece .name {
      font-family: "Roboto", Helvetica, Arial, sans-serif;
      color: #333333;
      color: #0087bc;
      text-align: center; }

.about-page {
  background: #4CAF50; }

